import React from "react";
import { useSelector } from "react-redux";
import ListItem from "./ListItem";

import { motion } from "framer-motion";
// const List = (props)
// props.todoData
// props.setTodoData
const List = React.memo(({ todoData, setTodoData, deleteClick }) => {
  // console.log("List Rendering...");
  const user = useSelector((state) => state.user);
  const list = {
    hidden: {
      opacity: 0,
    },
    visible: (i) => ({
      opacity: 1,
      transition: {
        when: "beforeChildren",
        staggerChildren: 0.2,
        duration: 1,
      },
    }),
  };
  const momo = {
    hidden: { opacity: 0, y: 50 },
    visible: { opacity: 1, y: 0 },
  };

  return (
    <motion.div variants={list} initial="hidden" animate="visible">
      {todoData.map(
        (item, i) =>
          item.author.uid === user.uid && (
            <motion.div key={item.id} variants={momo} custom={i}>
              <ListItem
                item={item}
                todoData={todoData}
                setTodoData={setTodoData}
                deleteClick={deleteClick}
              />
            </motion.div>
          )
      )}
    </motion.div>
  );
});

export default List;
